import { Component, OnInit, Input, ChangeDetectionStrategy } from '@angular/core';
import { PaymentCardNumberFormat, DefaultPaymentCardNumberFormat } from '@backbase/ui-ang/payment-card-number-pipe';

/**
 * Payment card data.
 */
export interface PaymentCard {
  /**
   * Unformatted payment card number.
   */
  number: number | string;
  /**
   * Payment card vendor.
   */
  vendor: string;
  /**
   * Name to be displayed on the payment card.
   */
  name: string;
  /**
   * Date the payment card expires.
   */
  expirationDate: Date;
  /**
   * Name of the card.
   */
  cardName: string;
}

/**
 * The prefix for the payment card class names.
 */
const classNamePrefix = 'bb-payment-card-';

@Component({
  selector: 'bb-payment-card-ui',
  templateUrl: './payment-card.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class PaymentCardComponent implements OnInit {
  /**
   * Payment card data.
   */
  @Input() paymentCard: PaymentCard | undefined;
  /**
   * Configuration of how the card number should be formatted.
   */
  @Input() paymentCardNumberFormat: PaymentCardNumberFormat = DefaultPaymentCardNumberFormat;

  constructor() {}

  ngOnInit() {
    if (typeof this.paymentCard === 'undefined') {
      throw new Error(`"paymentCard" input is required in "${this.constructor.name}"`);
    }
  }

  get classNames(): string | undefined {
    if (this.paymentCard && this.paymentCard.cardName) {
      const classNamePostfix = this.paymentCard.cardName
        .toLowerCase()
        .replace(/[^a-zA-Z0-9 ]/g, ' ')
        .trim()
        .replace(/\s/g, '-')
        .replace(/-{2,}/g, '-');

      return classNamePostfix ? `${classNamePrefix}${classNamePostfix}` : undefined;
    }

    return undefined;
  }
}
